import { Box, Typography } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import {motion} from 'framer-motion';
import React from 'react'

const HomePage = ()=>{
    const animation ={
        homeIcon:{
            initial:{
                x:"-70%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        homePara:{
            initial:{
                y:"90%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1.6 }
            }
        },
    }
    return(
        <>
        <Box id={"home"} height={"100vh"}  sx={{paddingTop:{xs:"20px",md:"100px"}}}>
            <motion.div {...animation.homeIcon}className={"homeIconContainer"}>
                <HomeIcon/><Typography>INTRODUCE</Typography>
            </motion.div>
            <Box sx={{width:{xs:"100%",md:"80%"},marginTop:{xs:"200px",sm:"250px",md:"0"}}}>
            <motion.div {...animation.homePara} className={"homePara"} >
                <Typography  className={"homeHandding"}
                sx={{fontSize:{xs:"2rem",sm:"3rem",md:"4rem"},textAlign:{xs:"center",md:"start"}
                ,width:{xs:"80%",md:"100%"},margin:{xs:"50px auto 0 auto",md:"0px auto 0 auto"}}}>Say Hi from <span>Drake</span>, Framer Designer and Developer
                </Typography >
                <Typography sx={{textAlign:{xs:"center",md:"start"}
                ,width:{xs:"50%",md:"80%"},margin:{xs:"20px auto 0 auto",md:"0"}}}>
                I design and code beautifully simple things and i love what i do. Just simple like that!
                </Typography>
            </motion.div>
            </Box>
        </Box>
        </>
    )
}

export default HomePage