import { Box, Typography } from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import PersonIcon from '@mui/icons-material/Person';

import {motion} from 'framer-motion';
import React from 'react'

const About = ()=>{
    const animation ={
        aboutIcon:{
            initial:{
                x:"-70%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        aboutPara:{
            initial:{
                y:"50%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1.6 }
            }
        },

    }
    return(
        <Box id='about' height={"100vh"} sx={{paddingTop:{xs:"20px",md:"100px"}}}>
            <motion.div {...animation.aboutIcon}className={"aboutIconContainer"}>
                <PersonIcon/><Typography>ABOUT</Typography>
            </motion.div>
            <Box sx={{width:{xs:"100%",md:"75%"}}}>

            </Box>
            <motion.div {...animation.aboutPara} className='aboutPara'>
                <Typography variant='h2' sx={{width:{xs:"80%"}
                ,margin:{xs:"170px auto 0 auto" ,sm:"200px auto 0 auto" ,md:"100px 0 0px 0"},fontSize:{xs:"2rem",sm:"3rem",md:"3.5rem"},textAlign:{xs:"center",md:"start"}}}>Every great design begin with an even <span>better story</span></Typography>
                <Typography sx={{textAlign:{xs:"center",md:"start"}
                ,width:{xs:"80%",md:"65%"},margin:{xs:"30px auto 0 auto",md:"50px 0 0 0"}}}>
                Since beginning my journey as a freelance designer nearly 8 years ago, 
                I've done remote work for agencies, consulted for startups,
                 and collaborated with talented people to create digital products for both business and consumer use. I'm quietly confident,
                 naturally curious, and perpetually working on improving my chopsone design problem at a time.
                </Typography>
            </motion.div>
        </Box>
    )
}

export default About