import { Box, Typography } from '@mui/material'
import React from 'react';
import {motion} from 'framer-motion';
import AttachmentIcon from '@mui/icons-material/Attachment';
import portfolio from '../img/portfolio1.jpg';
import portfolio1 from '../img/portfolio2.jpg';
import portfolio2 from '../img/portfolio3.jpg';
import portfolio3 from '../img/portfolio4.jpg';
import portfolio4 from '../img/portfolio5.jpg';

const portfolios = ()=>{
    const animation ={
        heading:{
            initial:{
                x:"-15%",
                opacity:0
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration:1}
            }
        },
        image:{
            initial:{
                x:"-10%",
                opacity:0
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration:1}
            }
        },
        image1:{
            initial:{
                x:"-15%",
                opacity:0
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration:1}
            }
        },
        image4:{
            initial:{
                x:"-10%",
                opacity:0
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration:1}
            }
        },
        image2:{
            initial:{
                x:"-15%",
                opacity:0
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration:1}
            }
        }
    }
    return(
        <Box id='portfolio' min-height={"100vh"} sx={{paddingLeft:{xs:"20px",md:"100px"}}} >
           <motion.div {...animation.ServicesIcon}className={"portfolioIconContainer"} >
                <AttachmentIcon/><Typography>PORTFOLIO</Typography>
            </motion.div>
            <motion.div className='heading' {...animation.heading}>
                <Typography variant='h2' sx={{fontSize:{xs:"2rem",sm:"3rem",md:"3.5rem"},textAlign:{xs:"center",md:"start"}}}>Featured <span>Projects</span></Typography>
            </motion.div>
            <Box className="container" sx={{width:{xs:"80%",sm:"70%",md:"80%"},margin:{xs:"0 auto",md:"0"}}} >
            <motion.div className='imageContainer' {...animation.image}>
                <img src={portfolio} alt="portfolio" />
            </motion.div>
            <Typography variant='h4' sx={{fontSize:{xs:"1.2rem",sm:"1.5rem",md:"1.5rem"},margin:"20px 0"}}>
                Bureau - Architecture Studio
            </Typography>
            </Box>
            <Box className="container2" sx={{display:{xs:"block",md:"flex"},width:{xs:"100%",md:"80%"},columnGap:{xs:"5px",md:"10px"}}}>
            <Box className="innerContainer" sx={{width:{xs:"80%",sm:"70%",md:"50%"}
            ,margin:{xs:"0 auto",md:"0"}}}>
            <motion.div className='imageContainer' {...animation.image1}>
                <img src={portfolio1} alt="portfolio" />
            </motion.div>
            <Typography variant='h5' sx={{fontSize:{xs:"1.2rem",sm:"1.5rem",md:"1.5rem"},margin:"20px 0"}}>
                Bureau - Architecture Studio
            </Typography>
            </Box>
            <Box className="innerContainer" sx={{width:{xs:"80%",sm:"70%",md:"50%"}
            ,margin:{xs:"0 auto",md:"0"}}}>
            <motion.div className='imageContainer' {...animation.image4}> 
                <img src={portfolio2} alt="portfolio" />
            </motion.div>
            <Typography variant='h5' sx={{fontSize:{xs:"1.2rem",sm:"1.5rem",md:"1.5rem"},margin:"20px 0"}}>
                Bureau - Architecture Studio
            </Typography>
            </Box>
            </Box>
            <Box className="container" sx={{width:{xs:"80%",sm:"70%",md:"80%"},margin:{xs:"0 auto",md:"0"}}}>
            <motion.div className='imageContainer' {...animation.image}>
                <img src={portfolio3} alt="portfolio" />
            </motion.div>
            <Typography variant='h4' sx={{fontSize:{xs:"1.2rem",sm:"1.5rem",md:"2rem"},margin:"20px 0"}}>
                Hinterland - Real Estate Site Redesign
            </Typography>
            </Box>
            <Box className="container" sx={{width:{xs:"80%",sm:"70%",md:"80%"},margin:{xs:"0 auto",md:"0"}}}>
            <motion.div className='imageContainer' {...animation.image1}>
                <img src={portfolio4} alt="portfolio" />
            </motion.div>
            <Typography variant='h4'  sx={{fontSize:{xs:"1.2rem",sm:"1.5rem",md:"2rem"},margin:"20px 0"}}>
                Hinterland - Real Estate Site Redesign
            </Typography>
            </Box>
        </Box>
    )
}

export default portfolios