import { Box, Grid, Typography } from '@mui/material'
import React from 'react';
import {motion} from 'framer-motion';
import AutoFixHighIcon from '@mui/icons-material/AutoFixHigh';
// import css from '../img/css.png'
// import express from '../img/expressjs.png'
// import html from '../img/html.png'
// import node from '../img/nodejs.png'
import react from '../img/react (1).png'
// import materialui from '../img/materialui.png'


const Skills = ()=>{
    const animation ={
        skillsIcons:{
            initial:{
                x:"-70%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        heading:{
            initial:{
                y:"50%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        skill1:{
            initial:{
                x:"-50%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        skill2:{
            initial:{
                y:"-50%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        skill3:{
            initial:{
                x:"20%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        skill5:{
            initial:{
                y:"5%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        skill6:{
            initial:{
                y:"-10%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        }
    }
    return(
        <Box id='skills' min-height={"100vh"} paddingTop={"90px"}>
             <motion.div {...animation.skillsIcons}className={"skillsIconContainer"}>
                <AutoFixHighIcon/><Typography>SKILLS</Typography>
            </motion.div>
            <motion.div {...animation.heading} className='heading'>
                <Typography variant='h2' sx={{fontSize:{xs:"2.5rem",md:"4rem"},textAlign:{xs:"center",md:"start"}}}>My <span>Advantages</span></Typography>
            </motion.div>
            <Grid container sx={{maxWidth:{xs:"100%",md:"80%"},rowGap:{xs:0,md:3}}} marginTop={"40px"}>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill1}>
                <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"90px",md:"120px"},height:{xs:"80px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>React js</Typography>
            </motion.div>
            </Grid>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill2}>
            <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"100px",md:"120px"},height:{xs:"90px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>Node js</Typography>
            </motion.div>
            </Grid>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill3}>
            <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"100px",md:"120px"},height:{xs:"90px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>HTML</Typography>
            </motion.div>
            </Grid>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill1}>
            <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"100px",md:"120px"},height:{xs:"90px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>CSS</Typography>
            </motion.div>
            </Grid>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill5}>
            <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"100px",md:"120px"},height:{xs:"90px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>EXPRESS js</Typography>
            </motion.div>
            </Grid>
            <Grid item xs={6} md={4} sm={4} >
            <motion.div className='skillscontainer' {...animation.skill6}>
            <Box className={"skill"}>
                <Box sx={{position:"relative",width:{xs:"100px",md:"120px"},height:{xs:"90px",md:"110px"}}}>
                    <img src={react} alt="react" style={{width:"100%",height:"100%",position:"absolute"}}/>
                </Box>
                </Box>
                <Typography>MONGODB</Typography>
            </motion.div>
            </Grid>
            </Grid>
        </Box>
    )
}

export default Skills