import { Box, Button, Grid, InputLabel, Snackbar, TextField, Typography } from '@mui/material'
import React, { useState } from 'react';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import {motion} from 'framer-motion';
import axios from 'axios';
import MuiAlert from '@mui/material/Alert';


const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });
  

const Contact = ()=>{
    const [openMassege , setOpenMassege] = useState({})
    const [firstName,setFirstName] = useState("")
    const [lastName,setLastName] = useState("")
    const [email,setEmail] = useState("")
    const [message,setMessage] = useState("")
    const [open, setOpen] = React.useState(false);
    const [budge,setBudge] = useState("")
    const [phon,setPhone] = useState("")

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
      };
    const animation ={
        contactIcon:{
            initial:{
                x:"-20%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        heading:{
            initial:{
                x:"-20%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        field1:{
            initial:{
                x:"-20%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        field2:{
            initial:{
                x:"-10%",
                opacity:0,
            },
            whileInView:{
                x:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        field5:{
            initial:{
                y:"40%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        },
        mail:{
            initial:{
                y:"60%",
                opacity:0,
            },
            whileInView:{
                y:0,
                opacity:1,
                transition:{duration: 1 }
            }
        }
    }
    const submitHandler = async(e)=>{
        e.preventDefault()
        try {
            const {data} = await axios.post("http://localhost:4000/api/contact",{
                firstName,
                lastName,
                budge,
                email,  
                phon,
                message
            })
            setOpenMassege(data)
            setOpen(true)
            setEmail("")
            setPhone("")
            setFirstName("")
            setLastName("")
            setBudge("")
            setMessage("")
        } catch (error) {
            setOpenMassege(error.response.data)
            setOpen(true)
        }
    }
    return(
        <Box id='contact' height={"100vh"} paddingTop={"100px"}>
            <motion.div {...animation.contactIcon} className={"contactIconContainer"}>
                <MailOutlineIcon/><Typography>CONTACT</Typography>
            </motion.div>
            <Box sx={{display:{xs:"flex",md:"block"},justifyContent:"center",alignItems:"center",flexDirection:"column"}}>
            <motion.div className='heading' {...animation.heading}>
                <Typography variant='h2' sx={{fontSize:{xs:"2rem",sm:"3rem",md:"3.5rem"},margin:{md:"40px 0"}}}>Let's Work <span>Together</span> !</Typography>
            </motion.div>
            <Box sx={{fontSize:{sm:"25px",md:"30px"},margin:{xs:"15px 0",md:"10px 0"}}}>
            <motion.div className={"mail"} {...animation.mail}><a href='mailto:ansarisageer972@gmail.com'>Ansarisageer972@.design</a></motion.div>
            </Box>
            <Grid container onSubmit={submitHandler} component={"form"} rowGap={{xs:1,md:2}} sx={{paddingLeft:{xs:"22px",md:"0"},width:{xs:"100%",md:"95%"}}} >
                <Grid item xs={6} className={"fields"} sx={{marginTop:{xs:"2px ",md:"6px"}}}>
                <motion.div {...animation.field1}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>First Name*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input onChange={(e)=>setFirstName(e.target.value)} value={firstName} style={{width:"100%"}} type={"text"} placeholder='Enter First Name...'/></Box>
                </motion.div>
                </Grid>
                <Grid item xs={6} className={"fields"}>
                <motion.div {...animation.field2}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>Last Name*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input value={lastName} onChange={(e)=>setLastName(e.target.value)} style={{width:"100%"}} type={"text"} placeholder='Enter First Name...'/></Box>
                </motion.div>
                </Grid>
                <Grid item xs={6} className={"fields"}>
                <motion.div {...animation.field1}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>Email*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input value={email} onChange={(e)=>setEmail(e.target.value)} style={{width:"100%"}}  type={"email"} placeholder='Enter First Name...'/></Box>
                </motion.div>
                </Grid>
                <Grid item xs={6} className={"fields"}>
                <motion.div {...animation.field2}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>Phone No(Optional)*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input value={phon} onChange={(e)=>setPhone(e.target.value)} style={{width:"100%"}} type={"number"} placeholder='Enter Email...'/></Box>
                </motion.div>
                </Grid >
                <Grid item xs={6} className={"fields"}>
                <motion.div {...animation.field1}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>Your Budge*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input onChange={(e)=>setBudge(e.target.value)} value={budge} style={{width:"100%"}} type={"text"} placeholder='Enter your budge...'/></Box>
                </motion.div>
                </Grid>
                <Grid item xs={6} className={"fields"}>
                <motion.div {...animation.field2}>
                    <InputLabel sx={{fontSize:{xs:"1rem",sm:"1.3rem",md:"1.5rem"}}}>Message*</InputLabel>
                    <Box sx={{width:{xs:"80%",md:"80%"}}}>
                        <input value={message} onChange={(e)=>setMessage(e.target.value)} style={{width:"100%"}} type={"text"} placeholder='Enter Your Message...'/></Box>
                </motion.div>
                </Grid>
                <Grid item xs={12} className={"fields"}>
                <motion.div className='buttonContainer' {...animation.field5}>
                    <Button type='submit' sx={{marginTop:{xs:"35px",md:"25px"}}}>Send Message</Button>
                </motion.div>
                </Grid>
            </Grid>
            </Box>
            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
      <Alert severity={openMassege.success?"success":"error"}>{openMassege.message}!</Alert>
      </Snackbar>
        </Box>
    )
}

export default Contact